const getStatusColor = (status: string) => {
  switch (status) {
    case "delivered":
      return "bg-green-100 text-green-800"
    case "in-transit":
      return "bg-blue-100 text-blue-800"
    case "processing":
      return "bg-yellow-100 text-yellow-800"
    case "cancelled":
      return "bg-red-100 text-red-800"
    default:
      return "bg-gray-100 text-gray-800"
  }
}

const getStatusIcon = (status: string) => {
  switch (status) {
    case "delivered":
      return <CheckCircle className="h-4 w-4" />
    case "in-transit":
      return <Truck className="h-4 w-4" />
    case "processing":
      return <Clock className="h-4 w-4" />
    case "cancelled":
      return <XCircle className="h-4 w-4" />
    default:
      return <Package className="h-4 w-4" />
  }
}

const getStatusText = (status: string) => {
  switch (status) {
    case "delivered":
      return "Delivered"
    case "in-transit":
      return "In Transit"
    case "processing":
      return "Processing"
    case "cancelled":
      return "Cancelled"
    default:
      return "Unknown"
  }
}

export function OrdersPage() {
  const [searchQuery, setSearchQuery] = useState("")
  const [statusFilter, setStatusFilter] = useState("all")
  const [selectedOrder, setSelectedOrder] = useState<(typeof orders)[0] | null>(null)

  const filteredOrders = orders.filter((order) => {
    const matchesSearch = order.id.toLowerCase().includes(searchQuery.toLowerCase())
    const matchesStatus = statusFilter === "all" || order.status === statusFilter

    return matchesSearch && matchesStatus
  })

  const ordersByStatus = {
    all: orders,
    processing: orders.filter((o) => o.status === "processing"),
    "in-transit": orders.filter((o) => o.status === "in-transit"),
    delivered: orders.filter((o) => o.status === "delivered"),
    cancelled: orders.filter((o) => o.status === "cancelled"),
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold">My Orders</h1>
          <p className="text-muted-foreground">Track and manage your solar equipment orders</p>
        </div>
        <Button variant="outline">
          <Download className="mr-2 h-4 w-4" />
          Export Orders
        </Button>
      </div>

      {/* Filters */}
      <Card>
        <CardContent className="p-4">
          <div className="flex flex-col sm:flex-row gap-4">
            <div className="relative flex-1">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder="Search orders by ID..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-10"
              />
            </div>
            <Select value={statusFilter} onValueChange={setStatusFilter}>
              <SelectTrigger className="w-full sm:w-48">
                <SelectValue placeholder="Filter by status" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Orders</SelectItem>
                <SelectItem value="processing">Processing</SelectItem>
                <SelectItem value="in-transit">In Transit</SelectItem>
                <SelectItem value="delivered">Delivered</SelectItem>
                <SelectItem value="cancelled">Cancelled</SelectItem>
              </SelectContent>
            </Select>
          </div>
        </CardContent>
      </Card>

      {/* Orders Tabs */}
      <Tabs defaultValue="all" className="space-y-6">
        <TabsList className="grid w-full grid-cols-5">
          <TabsTrigger value="all">All ({ordersByStatus.all.length})</TabsTrigger>
          <TabsTrigger value="processing">Processing ({ordersByStatus.processing.length})</TabsTrigger>
          <TabsTrigger value="in-transit">In Transit ({ordersByStatus["in-transit"].length})</TabsTrigger>
          <TabsTrigger value="delivered">Delivered ({ordersByStatus.delivered.length})</TabsTrigger>
          <TabsTrigger value="cancelled">Cancelled ({ordersByStatus.cancelled.length})</TabsTrigger>
        </TabsList>

        {Object.entries(ordersByStatus).map(([status, statusOrders]) => (
          <TabsContent key={status} value={status} className="space-y-4">
            {statusOrders.length === 0 ? (
              <Card>
                <CardContent className="p-12 text-center">
                  <Package className="h-16 w-16 text-muted-foreground mx-auto mb-4" />
                  <h3 className="text-lg font-semibold mb-2">No orders found</h3>
                  <p className="text-muted-foreground mb-6">
                    {status === "all" ? "You haven't placed any orders yet." : `No ${status} orders found.`}
                  </p>
                  <Button asChild>
                    <Link href="/products">Browse Products</Link>
                  </Button>
                </CardContent>
              </Card>
            ) : (
              statusOrders.map((order) => (
                <Card key={order.id}>
                  <CardContent className="p-6">
                    <div className="flex flex-col lg:flex-row lg:items-center justify-between gap-4 mb-4">
                      <div className="flex items-center gap-4">
                        <div>
                          <h3 className="font-semibold">{order.id}</h3>
                          <p className="text-sm text-muted-foreground">
                            Placed on {new Date(order.date).toLocaleDateString()}
                          </p>
                        </div>
                        <Badge className={getStatusColor(order.status)}>
                          {getStatusIcon(order.status)}
                          <span className="ml-1">{getStatusText(order.status)}</span>
                        </Badge>
                      </div>
                      <div className="flex items-center gap-2">
                        <span className="font-semibold">₦{order.total.toLocaleString()}</span>
                        <Dialog>
                          <DialogTrigger asChild>
                            <Button variant="outline" size="sm" onClick={() => setSelectedOrder(order)}>
                              <Eye className="mr-2 h-4 w-4" />
                              View Details
                            </Button>
                          </DialogTrigger>
                          <DialogContent className="max-w-2xl max-h-[80vh] overflow-y-auto">
                            <DialogHeader>
                              <DialogTitle>Order Details - {order.id}</DialogTitle>
                              <DialogDescription>
                                Placed on {new Date(order.date).toLocaleDateString()}
                              </DialogDescription>
                            </DialogHeader>
                            {selectedOrder && <OrderDetailsModal order={selectedOrder} />}
                          </DialogContent>
                        </Dialog>
                      </div>
                    </div>

                    <div className="space-y-3">
                      {order.items.map((item) => (
                        <div key={item.id} className="flex items-center gap-4">
                          <Image
                            src={item.image || "/placeholder.svg"}
                            alt={item.name}
                            width={60}
                            height={60}
                            className="rounded-md object-cover"
                          />
                          <div className="flex-1">
                            <p className="font-medium">{item.name}</p>
                            <p className="text-sm text-muted-foreground">
                              Quantity: {item.quantity} × ₦{item.price.toLocaleString()}
                            </p>
                          </div>
                          <div className="text-right">
                            <p className="font-medium">₦{(item.quantity * item.price).toLocaleString()}</p>
                          </div>
                        </div>
                      ))}
                    </div>

                    {order.status === "delivered" && (
                      <div className="flex gap-2 mt-4 pt-4 border-t">
                        <Button variant="outline" size="sm">
                          <Star className="mr-2 h-4 w-4" />
                          Write Review
                        </Button>
                        <Button variant="outline" size="sm">
                          <Package className="mr-2 h-4 w-4" />
                          Reorder
                        </Button>
                        <Button variant="outline" size="sm">
                          <MessageSquare className="mr-2 h-4 w-4" />
                          Get Support
                        </Button>
                      </div>
                    )}

                    {order.status === "in-transit" && order.shipping.trackingNumber && (
                      <div className="mt-4 pt-4 border-t">
                        <Button variant="outline" size="sm" asChild>
                          <Link href={`/track-order?tracking=${order.shipping.trackingNumber}`}>
                            <Truck className="mr-2 h-4 w-4" />
                            Track Package
                          </Link>
                        </Button>
                      </div>
                    )}
                  </CardContent>
                </Card>
              ))
            )}
          </TabsContent>
        ))}
      </Tabs>
    </div>
  )
}

function OrderDetailsModal({ order }: { order: (typeof orders)[0] }) {
  return (
    <div className="space-y-6">
      {/* Order Status */}
      <div className="flex items-center gap-2">
        <Badge className={getStatusColor(order.status)}>
          {getStatusIcon(order.status)}
          <span className="ml-1">{getStatusText(order.status)}</span>
        </Badge>
        {order.shipping.trackingNumber && (
          <span className="text-sm text-muted-foreground">Tracking: {order.shipping.trackingNumber}</span>
        )}
      </div>

      {/* Order Items */}
      <div>
        <h4 className="font-semibold mb-3">Order Items</h4>
        <div className="space-y-3">
          {order.items.map((item) => (
            <div key={item.id} className="flex items-center gap-4 p-3 border rounded-lg">
              <Image
                src={item.image || "/placeholder.svg"}
                alt={item.name}
                width={60}
                height={60}
                className="rounded-md object-cover"
              />
              <div className="flex-1">
                <p className="font-medium">{item.name}</p>
                <p className="text-sm text-muted-foreground">
                  ₦{item.price.toLocaleString()} × {item.quantity}
                </p>
              </div>
              <div className="text-right">
                <p className="font-medium">₦{(item.quantity * item.price).toLocaleString()}</p>
              </div>
            </div>
          ))}
        </div>
      </div>

      <Separator />

      {/* Shipping Information */}
      <div>
        <h4 className="font-semibold mb-3">Shipping Information</h4>
        <div className="bg-muted p-3 rounded-lg">
          <p className="font-medium">{order.shipping.method}</p>
          <p className="text-sm text-muted-foreground">{order.shipping.address}</p>
          {order.shipping.trackingNumber && (
            <p className="text-sm text-muted-foreground mt-1">Tracking: {order.shipping.trackingNumber}</p>
          )}
        </div>
      </div>

      {/* Payment Information */}
      <div>
        <h4 className="font-semibold mb-3">Payment Information</h4>
        <div className="bg-muted p-3 rounded-lg">
          <p className="font-medium">{order.payment.method}</p>
          {order.payment.last4 && <p className="text-sm text-muted-foreground">Card ending in {order.payment.last4}</p>}
        </div>
      </div>

      <Separator />

      {/* Order Summary */}
      <div>
        <h4 className="font-semibold mb-3">Order Summary</h4>
        <div className="space-y-2">
          <div className="flex justify-between">
            <span className="text-muted-foreground">Subtotal</span>
            <span>₦{order.total.toLocaleString()}</span>
          </div>
          <div className="flex justify-between">
            <span className="text-muted-foreground">Shipping</span>
            <span>Free</span>
          </div>
          <Separator />
          <div className="flex justify-between font-semibold">
            <span>Total</span>
            <span>₦{order.total.toLocaleString()}</span>
          </div>
        </div>
      </div>
    </div>
  )
}
